.contact-page {
   
    text-align: center;
  }
  .contact-box {
    display: flex;
    align-items: center;
    background: linear-gradient(to left, #ee5220, #ffcc66);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 20px;
    max-height: auto;
  }
  .contact-left {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .contact-image {
    max-width: 100%;
  height: 200px;
    border-radius: 10px;
  }
  .contact-right {
    flex: 1;
    text-align: left;
    padding-left: 20px;
  }
  .contact-right p {
    margin-bottom: 10px;
    font-size: 2rem;
  }
  .enquiry-form {
    margin-top: 30px;
    max-width: 500px;
    margin: 0 auto;
    text-align: left;
    background: linear-gradient(to right, #ee5220, #ffcc66);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  .form-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }
  label {
    flex: 1;
    margin-right: 10px;
    font-weight: bold;
  }
  input[type="text"],
  input[type="email"] {
    flex: 2;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    outline: none;
  }
  input[type="text"]:focus,
  input[type="email"]:focus {
    border-color: #ee5220;
  }
  button {
    background-color: #ee5220;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 1em;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  button:hover {
    background-color: #d94a1e;
  }
  .submit-button {
    display: flex;
    justify-content: center;
  }
  @media(max-width:600px){
    .contact-box{
      flex-direction: column;
    }
    .contact-right{
      padding-left: 10px;
      margin-top: 20px;
      padding-bottom: 20px;
    }
  }